import { FormData } from './types';

interface FieldConfig {
  label: string;
  value: string;
  id: string;
  labelDescription: string;
  valueDescription: string;
  required?: boolean;
  className?: string;
  itemLabel?: string;
  isItemLabelEditable?: boolean;
}

export const getPatientFields = (formData: FormData): FieldConfig[] => [
  {
    label: 'patient_first_name',
    value: formData.patientFirstName,
    id: 'patientFirstName',
    labelDescription: 'The patient`s first name',
    valueDescription: 'Letters only, up to 50 characters',
    required: true,
  },
  {
    label: 'patient_last_name',
    value: formData.patientLastName,
    id: 'patientLastName',
    labelDescription: 'The patient`s last name',
    valueDescription: 'Letters only, up to 50 characters',
    required: true,
  },
  {
    label: 'patient_dob',
    value: formData.dob,
    id: 'dob',
    labelDescription: 'The patient`s date of birth',
    valueDescription: 'YYYY-MM-DD',
    required: true,
  },
];

