import React, { useState, useEffect, useCallback } from 'react';
import { Tooltip, SwitchNew, Popover, usePopoverState } from '@getvim/atomic-ui';
import classNames from 'classnames';

interface DataInputFieldProps {
  label: string;
  itemLabel?: string;
  value?: string;
  defaultValue?: string;
  onChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
  className?: string;
  id?: string;
  labelDescription?: string;
  valueDescription?: string;
  disabled?: boolean;
  onDisabledChange?: (isDisabled: boolean) => void;
  onLabelChange?: (newLabel: string) => void;
  isItemLabelEditable?: boolean;
  required?: boolean;
}

const DataInputField: React.FC<DataInputFieldProps> = ({
  label,
  itemLabel,
  value,
  defaultValue = '',
  onChange,
  className,
  id,
  labelDescription,
  valueDescription,
  disabled = false,
  onDisabledChange,
  onLabelChange,
  isItemLabelEditable = false,
  required = false,
}) => {
  const [internalValue, setInternalValue] = useState(defaultValue);
  const [isDisabled, setIsDisabled] = useState(disabled);
  const [newLabel, setNewLabel] = useState(itemLabel ?? label);
  const [showRequiredError, setShowRequiredError] = useState(false);
  const labelPopover = usePopoverState({
    placement: 'top',
  });

  useEffect(() => {
    setIsDisabled(disabled);
  }, [disabled]);

  const handleChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const newValue = event.target.value;
      setInternalValue(newValue);
      setShowRequiredError(required && !newValue);
      onChange(event);
    },
    [onChange, required],
  );

  const handleLabelChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      setNewLabel(event.target.value);
      if (onLabelChange) {
        onLabelChange(event.target.value);
      }
    },
    [onLabelChange],
  );

  const toggleDisabledState = useCallback(() => {
    if (!disabled) {
      const newDisabledState = !isDisabled;
      setIsDisabled(newDisabledState);
      onDisabledChange?.(newDisabledState);
    }
  }, [disabled, isDisabled, onDisabledChange]);

  const handleBlur = useCallback(() => {
    if (required && !(value ?? internalValue)) {
      setShowRequiredError(true);
    }
  }, [required, value, internalValue]);

  return (
    <div
      className={classNames('data-input-field', label.toLowerCase().replace(/_/g, '-'), className, {
        disabled: isDisabled,
      })}
      id={id}
    >
      <div className="label-container">
        <label htmlFor={`${id}-input`}>
          {label}
          {required && <span className="required-indicator">*</span>}
        </label>
        {labelDescription && (
          <Tooltip tooltipContent={labelDescription} placement="top" width="large">
            <i className="icon-help-c-2" />
          </Tooltip>
        )}
        {isItemLabelEditable && (
          <div style={{ position: 'relative' }}>
            <Tooltip tooltipContent="Edit title" placement="top">
              <i
                className="icon-pencil"
                onClick={labelPopover.toggle}
                style={{ cursor: 'pointer' }}
              />
            </Tooltip>
            <Popover
              popover={labelPopover}
              className="label-edit-popover"
              aria-label="Edit title"
              withArrow
            >
              <div className="popover-content">
                <input
                  type="text"
                  className="input-field"
                  value={newLabel}
                  onChange={handleLabelChange}
                  placeholder="Title"
                />
              </div>
            </Popover>
          </div>
        )}
      </div>
      <div className="input-container">
        <input
          type="text"
          className={`input-field field-value ${showRequiredError ? 'error' : ''}`}
          value={value ?? internalValue}
          onChange={handleChange}
          id={`${id}-input`}
          disabled={isDisabled}
          onBlur={handleBlur}
          placeholder={showRequiredError ? 'Required field' : ''}
        />
      </div>
      <div className="switch-container">
        <div className="switch-wrap">
          <SwitchNew
            checked={!isDisabled}
            onChange={toggleDisabledState}
            className="toggle-disabled-switch"
            id={`${id}-switch`}
            disabled={disabled}
          />
        </div>

        {valueDescription && (
          <Tooltip
            tooltipContent={
              <>
                <span>Valid value:</span>
                <br />
                {valueDescription}
              </>
            }
            placement="top"
            referenceClassName="requirements-tooltip"
            width="large"
          >
            <i className="input-tooltip-icon icon-check-circle-2" />
          </Tooltip>
        )}
      </div>
    </div>
  );
};

export default DataInputField;
