import React, { useState, useCallback } from 'react';
import classNames from 'classnames';
import DataInputField from './DataInputField';

interface InputsGroupProps {
  title: string;
  fields: {
    id: string;
    label: string;
    value: string;
    labelDescription?: string;
    valueDescription?: string;
    disabled?: boolean;
    itemLabel?: string;
    isItemLabelEditable?: boolean;
    required?: boolean;
    className?: string;
  }[];
  onInputChange: (fieldId: string, value: string) => void;
  onLabelChange?: (fieldId: string, newLabel: string) => void;
}

const InputsGroup: React.FC<InputsGroupProps> = ({
  title,
  fields,
  onInputChange,
  onLabelChange,
}) => {
  const [isCollapsed, setIsCollapsed] = useState(false);

  const toggleCollapse = useCallback(() => {
    setIsCollapsed((prev) => !prev);
  }, []);

  const handleInputChange = useCallback(
    (fieldId: string) => (e: React.ChangeEvent<HTMLInputElement>) => {
      onInputChange(fieldId, e.target.value);
    },
    [onInputChange],
  );

  const handleLabelChange = useCallback(
    (fieldId: string) => (newLabel: string) => {
      onLabelChange?.(fieldId, newLabel);
    },
    [onLabelChange],
  );

  return (
    <div className={classNames('inputs-group', { 'is-collapsed': isCollapsed })}>
      <div className="input-group-title" onClick={toggleCollapse} style={{ cursor: 'pointer' }}>
        <h2>{title}</h2>
        <div className="collapse-icon">
          {isCollapsed ? <i className="icon-chevron-down" /> : <i className="icon-chevron-up" />}
        </div>
      </div>
      <div className="inputs-group-content">
        <div className="inputs-group-inner">
          {fields.map((field) => (
            <DataInputField
              key={field.id}
              label={field.label}
              value={field.value}
              onChange={handleInputChange(field.id)}
              onLabelChange={onLabelChange ? handleLabelChange(field.id) : undefined}
              id={field.id}
              labelDescription={field.labelDescription || ''}
              valueDescription={field.valueDescription || ''}
              disabled={field.disabled}
              itemLabel={field.itemLabel}
              isItemLabelEditable={field.isItemLabelEditable}
              required={field.required}
              className={classNames(field.className, 'data-input-field')}
            />
          ))}
        </div>
      </div>
    </div>
  );
};

export default InputsGroup;
